section {
    height:200px;
    background: url(../boat4.jpg);
    background-size: cover;
    background-position: center;
    position: relative;
}

.overlay {
    height:inherit;
    background: rgba(16,137,255,.8);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color:white;
}

.overlay .button {
    background: orange;
    padding:10px;
    margin:10px;
}

.overlay .button a {
    text-decoration: none;
    color:white;
}