.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-transform: uppercase;
}

.header .header-title {
    font-size: 20px;
    padding: 20px 0;
    color:#1089FF;
}

.nav-div {
    background: #1089FF;
    width: 100%;
    display:flex;
    justify-content: center;
    font-size:20px;
    
}

.nav-bar {
    display: flex;
    gap:20px;
    padding:20px;

}

.nav-bar a {
    text-decoration: none;
    color:white;
}
